"use client";

import ScrollAnimation from '../components/ScrollAnimation';
import { useProjectPage } from '../hooks/useProjectPage';
import Image from 'next/image';

export default function WorksPage() {
  const { data, loading, error } = useProjectPage();

  // Convert data to array and sort by most recent
  const projects = data
    ? Object.entries(data)
        .map(([id, project]) => ({ id, ...project }))
        .sort((a, b) => new Date(b.created_at).getTime() - new Date(a.created_at).getTime())
    : [];

  // Responsive grid columns
  const gridCols =
    'grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-8 md:gap-10 lg:gap-12';

  // Color schemes for cards
  const colorSchemes = [
    'bg-teal-100 border-teal-300',
    'bg-red-100 border-red-300',
    'bg-yellow-100 border-yellow-300',
    'bg-blue-100 border-blue-300',
    'bg-purple-100 border-purple-300',
    'bg-orange-100 border-orange-300',
  ];

  return (
    <div className="min-h-screen w-full bg-[#F5F5F5] py-16 md:py-24 lg:py-32 px-4 md:px-12 lg:px-32 xl:px-56">
      {/* Header */}
      <div className="text-center mb-12 md:mb-20">
        <h1 className="text-[#0A2F3B] text-[32px] md:text-[60px] lg:text-[80px] font-bold mb-4 md:mb-6">
          All Projects
        </h1>
        <p className="text-gray-600 text-[16px] md:text-[20px] lg:text-[24px] max-w-3xl mx-auto">
          Explore my latest projects, digital solutions, and creative work.
        </p>
      </div>

      {/* Loading State */}
      {loading && (
        <div className="flex flex-col items-center justify-center min-h-[300px]">
          <div className="animate-spin rounded-full h-20 w-20 border-b-2 border-[#286F6E] mb-4"></div>
          <p className="text-lg text-gray-600">Loading projects...</p>
        </div>
      )}

      {/* Error State */}
      {error && (
        <div className="flex flex-col items-center justify-center min-h-[300px]">
          <p className="text-lg text-red-500">Error loading projects: {error}</p>
        </div>
      )}

      {/* Empty State */}
      {!loading && !error && projects.length === 0 && (
        <div className="flex flex-col items-center justify-center min-h-[300px]">
          <p className="text-lg text-gray-500">No projects found.</p>
        </div>
      )}

      {/* Grid of Projects */}
      {!loading && !error && projects.length > 0 && (
        <div className={gridCols}>
          {projects.map((project, idx) => (
            <ScrollAnimation key={project.id} delay={idx * 0.1}>
              <div
                className={`relative group rounded-3xl border-2 shadow-lg p-8 md:p-10 transition-all duration-300 cursor-pointer hover:scale-[1.03] hover:shadow-2xl ${
                  colorSchemes[idx % colorSchemes.length]
                }`}
                tabIndex={0}
                aria-label={`Project: ${project.title}`}
              >
                {/* Project Image */}
                <div className="w-full h-40 md:h-56 rounded-xl overflow-hidden mb-6 bg-white flex items-center justify-center">
                  <Image
                    src={project.image_url || '/assets/crmc.png'}
                    alt={project.title}
                    width={400}
                    height={224}
                    className="object-cover w-full h-full"
                  />
                </div>
                {/* Title & Type */}
                <div className="mb-2">
                  <h2 className="text-2xl md:text-3xl font-bold text-[#286F6E] group-hover:underline">
                    {project.title}
                  </h2>
                  <span className="inline-block mt-1 px-3 py-1 rounded-full bg-white text-[#286F6E] text-xs font-semibold border border-[#286F6E]">
                    {project.type}
                  </span>
                </div>
                {/* Description */}
                <p className="text-gray-600 text-base md:text-lg mb-4 line-clamp-4 group-hover:line-clamp-none transition-all duration-300">
                  {project.description}
                </p>
                {/* Links */}
                <div className="flex gap-4 mt-4">
                  {project.project_url && (
                    <a
                      href={project.project_url}
                      target="_blank"
                      rel="noopener noreferrer"
                      className="px-4 py-2 rounded-full bg-[#286F6E] text-white font-semibold text-sm hover:bg-[#1e5a59] transition-colors duration-300 shadow-md border border-[#286F6E]"
                      title="See Live Project"
                      onClick={e => e.stopPropagation()}
                    >
                      Live
                    </a>
                  )}
                  {project.github_url && (
                    <a
                      href={project.github_url}
                      target="_blank"
                      rel="noopener noreferrer"
                      className="px-4 py-2 rounded-full bg-gray-800 text-white font-semibold text-sm hover:bg-gray-700 transition-colors duration-300 shadow-md border border-gray-800"
                      title="GitHub Repository"
                      onClick={e => e.stopPropagation()}
                    >
                      GitHub
                    </a>
                  )}
                </div>
                {/* Created/Updated Info */}
                <div className="absolute bottom-6 right-8 text-xs text-gray-500">
                  <span>Created: {project.created_at}</span>
                  <span className="ml-2">Updated: {project.updated_at}</span>
                </div>
              </div>
            </ScrollAnimation>
          ))}
        </div>
      )}
    </div>
  );
} 